.droppable-container {
  width: 100%;
  height: 100%;
  position: relative;
}

.droppable-container__overlay {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
}

.droppable-container--open {
  background-image: -webkit-repeating-linear-gradient(
    135deg,
    rgb(236, 240, 241, .5),
    rgb(236, 240, 241, .5) 3px,
    transparent 1px,
    transparent 10px
  );
  background-image: -o-repeating-linear-gradient(
    135deg,
    rgb(236, 240, 241, .5),
    rgb(236, 240, 241, .5) 3px,
    transparent 1px,
    transparent 10px
  );
  background-image: repeating-linear-gradient(
    -45deg,
    rgb(236, 240, 241, .5),
    rgb(236, 240, 241, .5) 3px,
    transparent 1px,
    transparent 10px
  );
  -webkit-animation: progress 2s linear infinite;
          animation: progress 2s linear infinite;
  background-size: 150% 100%;
}

.droppable-container--over {
    background-color: rgb(236, 240, 241, .5);
}

@-webkit-keyframes progress {
  0% {
    background-position: 0 0;
  }
  100% {
    background-position: -75px 0px;
  }
}
@keyframes progress {
  0% {
    background-position: 0 0;
  }
  100% {
    background-position: -70px 0px;
  }
}



